import { useState } from 'react';
import { Button } from '../ButtonElement';
import Video from '../../video/video.mp4';
import {
  HeroPageContainer,
  HeroPageBg,
  HeroPageVideoBg,
  HeroPageH1,
  HeroPageContent,
  HeroPageBtnContainer,
  HeroPageP
} from './style';

import { useCartStorage, useUserStorage } from "../../services/storageAdapter";

const HeroPage = () => {
  const [hover, setHover] = useState(false)

  const onHover = () => {
    setHover(!hover)
  }

  const { user } = useUserStorage();
  const { cart } = useCartStorage();

  return (
    <>
      <HeroPageContainer>
        <HeroPageBg>
          <HeroPageVideoBg autoPlay loop muted src={Video}></HeroPageVideoBg>
        </HeroPageBg>
        <HeroPageContent>
          <HeroPageH1>
            Welcome to Team32 game explorer!
          </HeroPageH1>
          <HeroPageP>
            We are gladly sharing our games, search, and machine learning recommendations with the world.
          </HeroPageP>
          <HeroPageBtnContainer>
              <Button to={!user ? "/auth" : "/user"} 
                onMouseEnter={onHover}
                onMouseLeave={onHover}
                primary='true'
                dark='true'
                big='true'
              >
                start
              </Button>
          </HeroPageBtnContainer>
        </HeroPageContent>
      </HeroPageContainer>
    </>
  )
}

export default HeroPage

