import { Checkbox } from "./Checkbox";
import { useUserStorage } from "../../services/storageAdapter";


export function Profile() {
  const { user, updateUser } = useUserStorage();
  if (!user) return null;
  return (
    <>
      <h1>{user.name}</h1>
      <h2>{user.email}</h2>
      <h2>Platform you have</h2>
      <Checkbox name='xbox'/>
      <Checkbox name='pc'/>
      <Checkbox name='playstation'/>
      <Checkbox name='nintendo'/>
    </>
  );
}
