import { Link } from "react-router-dom";
import styled from "styled-components";

export const NavbarContainer = styled.nav`
  height: 80px;
  position: fixed;
  top: 0px;
  color: white;
  background-color: #333;
  width: 100%;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
`;

export const NavbarTextLogo = styled(Link)`
  font-size: 25px;
  font-weight: 700;
  color: white;
  margin-left: 20px;
  text-decoration: none;
  cursor: pointer;
`

export const NavbarLinkContainer = styled.div`
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  padding-left: 12px;

  @media screen and (max-width: 760px) {
    display: none;
  }
`

export const NavbarLink = styled.div`
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 20px;
  transition: all 0.2s ease-in-out;
  border-radius: 20px;
  cursor: pointer;
  margin-left: 12px;
  margin-right: 12px;

  :hover {
    background-color: #ddd;
    color: black;
  }

  @media screen and (max-width: 760px) {
    margin-top: 12px;
    margin-bottom: 12px;
    font-size: 16px;
  }
`

export const NavbarForm = styled.form`
  margin-left: 12px;
  padding-right: 20px;
  display: flex;
  text-align: center;
  align-items: center;
  font-size: 20px;

  @media screen and (max-width: 760px) {
    margin-top: 12px;
    margin-bottom: 12px;
    font-size: 16px;
  }
`

export const NavbarInput = styled.input`
  padding: 8px;
  border: none;
  height: 30px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
`

export const NavbarButton = styled.button`
  padding: 8px;
  border: none;
  height: 30px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
`

export const NavbarMobileButton = styled.div`
  display: none;
  
  @media screen and (max-width: 760px) {
    display: flex;
    width: 50px;
    height: 50px;
    margin-right: 20px;
    font-size: 30px;
    cursor: pointer;
    text-align: center;
    align-item: center;
    justify-content: center;
    transition: all 0.2s ease-in-out;
    border-radius: 10px;
    padding-top: 3px;
    color: ${({toggle}) => (toggle ? '#000' : '#fff')};
    background-color: ${({toggle}) => (toggle ? '#fff' : '#333')};
  }
`

export const NavbarContainerMobile = styled.div`
  display: none;
  transition: all 0.2s ease-in-out;

  @media screen and (max-width: 760px) {
    display: flex;
    opacity: ${({toggle}) => (toggle ? '1' : '0')};
    top: ${({ toggle }) => (toggle ? '80px' : '-100%')};
    width: 100%;
    flex-direction: column;
    position: fixed;
    z-index: 9;
    background-color: #444;
    align-items: center;
  }
`