import { useState } from 'react';
import { NavbarButton, NavbarContainer, NavbarContainerMobile, NavbarTextLogo, NavbarForm, NavbarInput, NavbarLink, NavbarLinkContainer, NavbarMobileButton } from './style';

import { Link } from "react-router-dom";
import { useCartStorage, useUserStorage } from "../../services/storageAdapter";

const Navbar = () => {
  const [toggle, setToggle] = useState(false)

  const isToggle = () => {
    setToggle(!toggle);
  }
  
  const { user } = useUserStorage();
  const { cart } = useCartStorage();

  return (
    <>
      <NavbarContainer>
            <NavbarTextLogo to='/'>I Love Gaming!!! üéÆ</NavbarTextLogo>
        <NavbarLinkContainer>
          <Link to="/">
            <NavbarLink>
              Main
            </NavbarLink>
          </Link>
          <Link to="/game">
            <NavbarLink>
              Game
            </NavbarLink>
          </Link>
          {!user ? (
          <Link to="/auth"><NavbarLink>Sign in</NavbarLink></Link>
          ) : (
          <Link to="/user">
              <NavbarLink>{user.name} ({cart.products.length})</NavbarLink>
          </Link>
          )}
          
          <NavbarForm>
            <NavbarInput type="text" placeholder='Search...' />
            <NavbarButton type="submit">üîç</NavbarButton>
          </NavbarForm>
        </NavbarLinkContainer>
        <NavbarMobileButton toggle={toggle} onClick={isToggle}>‚ò∞</NavbarMobileButton>
      </NavbarContainer>
      <NavbarContainerMobile toggle={toggle}>
        <Link to="/">
          <NavbarLink>
            Main
          </NavbarLink>
        </Link>
        <Link to="/game">
          <NavbarLink>
            Game
          </NavbarLink>
        </Link>

        {!user ? (
          <Link to="/auth"><NavbarLink>Sign in</NavbarLink></Link>
          ) : (
          <Link to="/user">
              <NavbarLink>{user.name} ({cart.products.length})</NavbarLink>
          </Link>
        )}

        <NavbarForm>
          <NavbarInput type="text" placeholder='Search...' />
          <NavbarButton type="submit">üîç</NavbarButton>
        </NavbarForm>
      </NavbarContainerMobile>
    </>
  )
}

export default Navbar;
