import { hasPreference, User } from "../../domain/user";
import { useUserStorage } from "../../services/storageAdapter";

const platform = ["xbox", "pc", "playstation", "nintendo"];

export function Checkbox(props: {name: Ingredient}) {
  const { user, updateUser } = useUserStorage();
  if (!user) return <></>;
  const name = props.name;
  const onClick = (name: Ingredient) => {
    const newUser: User = {
      ...user,
      preferences: [...user.preferences]
    };
    if (hasPreference(user, name)) {
      newUser.preferences = user.preferences.filter(p => p !== name);
    } else {
      newUser.preferences.push(name);
    }
    updateUser(newUser);
  };
  return (
    <div>
      <input 
        type='checkbox'
        checked={user.preferences.includes(name) ? true : undefined}
        onClick={() => onClick(name)}
      />
      {name}
    </div>
  );
}
