import styled from "styled-components";

export const HeroPageContainer = styled.div`
  top: 100;
  background-color: black;
  width: 100vw;
  height: 100vh;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  z-index: 1;

  :before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0.6) 100%),
    linear-gradient(100deg, rgba(0, 0, 0, 0.2) 0%, transparent 100%);
    z-index: 2;
  }
`;

export const HeroPageBg = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
`

export const HeroPageVideoBg = styled.video`
  width: 100%;
  height: 100%;
  object-fit: cover;
`

export const HeroPageContent = styled.div`
  z-index: 3;
  width: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
`

export const HeroPageH1 = styled.h1`
  color: white;
  font-size: 48px;

  @media screen and (max-width: 760px) {
    font-size: 40px;    
  }
`

export const HeroPageP = styled.p`
  color: white;
  font-size: 24px;
  text-align: center;
  margin: 10px;

  @media screen and (max-width: 760px) {
    font-size: 16px;
  }
`

export const HeroPageBtnContainer = styled.div`
  margin-top: 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
`